<template>
  <div>
    <div id="container"></div>
  </div>
</template>
 
<script>
import * as Three from "three";

export default {
  name: "ThreeTest",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      shape: []
    };
  },
  methods: {
    init: function() {
      let container = document.getElementById("container");

      //
      let blue = new Three.Color(0x7658ef);
      let pink = new Three.Color(0xfca4c5);

      //
      this.scene = new Three.Scene();

      //
      this.camera = new Three.PerspectiveCamera(
        70,
        container.clientWidth / container.clientHeight,
        0.01,
        10
      );
      this.camera.position.z = 1;

      //{ alpha: true }
      this.renderer = new Three.WebGLRenderer({ antialias: true, alpha: true });

      //
      this.renderer.setSize(container.clientWidth, container.clientHeight);

      //canvascanvasthree.js
      container.appendChild(this.renderer.domElement);

      // var shape = [];

      //IcosahedronGeometry(radius,detail) 
      let geometry = new Three.IcosahedronGeometry(2, 0);

      //MeshNormalMaterial({ color: 0x0000ff})  RGB 

      // let material = new Three.MeshNormalMaterial({ color: 0x0000ff });
      let material = new Three.MeshNormalMaterial();

      //Mesh(geometry,material)
      this.shape[0] = new Three.Mesh(geometry, material); //
      this.shape[1] = new Three.Mesh(geometry, material); //
      this.shape[2] = new Three.Mesh(geometry, material); //

      // x y z
      this.shape[0].position.set(3, 5, 0);
      this.shape[1].position.set(3, 5, 0);
      this.shape[2].position.set(3, 5, 0);

      //
      this.scene.add(this.shape[0], this.shape[1], this.shape[2]);

      // PointLight(hex, intensity, distance, decay)

      var light = new Three.PointLight(0xfca4c5);

      // x y z
      light.position.set(0, 250, 0);

      //
      this.scene.add(light);

      // x y z
      this.camera.position.set(3, 4, 10);
    },
    animate: function() {
      requestAnimationFrame(this.animate);
      //x,y,z
      this.shape[0].rotation.x += 0.035;
      this.shape[0].rotation.y -= 0.005;
      this.shape[1].rotation.y += 0.015;
      this.shape[1].rotation.z -= 0.005;
      this.shape[2].rotation.z -= 0.025;
      this.shape[2].rotation.x += 0.005;
      this.renderer.render(this.scene, this.camera);
    }
  },
  mounted() {
    this.init();
    this.animate();
  }
};
</script>
<style scoped>
#container {
  height: 400px;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



