<template>
  <div>
    <div id="d3d1"></div>
  </div>
</template>
 
<script>
import {
  Scene,
  Color,
  PerspectiveCamera,
  WebGLRenderer,
  IcosahedronGeometry,
  CylinderBufferGeometry,
  MeshNormalMaterial,
  Mesh,
  PointLight,
  CameraHelper
} from "three";

import OrbitControls from "three/examples/js/controls/OrbitControls";

export default {
  name: "d3d1",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      shape: []
    };
  },
  mounted() {
    this.init();
    // this.animate();
    requestAnimationFrame(this.animate);
    this.renderer.render(this.scene, this.camera);

    window.onresize = () => {
      // onresize
      let container = document.getElementById("d3d1");
      this.camera.aspect = container.clientWidth / container.clientHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      // this.init();
      // this.animate();
    };
  },
  // created(){
  //   this.init()
  // },
  methods: {
    init: function() {
      let container = document.getElementById("d3d1");

      //
      let blue = new Color(0x7658ef);
      let pink = new Color(0xfca4c5);

      let THREE = {
        Scene,
        Color,
        PerspectiveCamera,
        WebGLRenderer,
        IcosahedronGeometry,
        CylinderBufferGeometry,
        MeshNormalMaterial,
        Mesh,
        PointLight,
        CameraHelper
      };

      //
      this.scene = new THREE.Scene();

      //
      this.camera = new THREE.PerspectiveCamera(
        90,
        container.clientWidth / container.clientHeight,
        0.01,
        120
      );
      this.camera.position.z = 1;

      //{ alpha: true }  antialias:
      this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });

      //
      this.renderer.setSize(container.clientWidth, container.clientHeight);

      //canvascanvasTHREE.js
      container.appendChild(this.renderer.domElement);

      var orbit = new OrbitControls(this.camera, this.renderer.domElement);

      // var shape = [];

      //IcosahedronGeometry(radius,detail) 
      // let geometry = new THREE.IcosahedronGeometry(2.5, 0);
      let geometry = new THREE.CylinderBufferGeometry(5, 5, 20, 32);

      //MeshNormalMaterial({ color: 0x0000ff})  RGB 

      // let material = new THREE.MeshNormalMaterial({ color: 0x0000ff });
      let material = new THREE.MeshNormalMaterial();

      //Mesh(geometry,material)
      this.shape[0] = new THREE.Mesh(geometry, material); //
      // this.shape[1] = new THREE.Mesh(geometry, material); //
      // this.shape[2] = new THREE.Mesh(geometry, material); //

      // x y z
      this.shape[0].position.set(3, 5, -10);
      // this.shape[1].position.set(3, 5, 0);
      // this.shape[2].position.set(3, 5, 0);

      //
      // this.scene.add(this.shape[0], this.shape[1], this.shape[2]);
      this.scene.add(this.shape[0]);

      // PointLight(hex, intensity, distance, decay)

      var light = new THREE.PointLight(0xfca4c5);

      // x y z
      light.position.set(0, 250, 0);

      //
      this.scene.add(light);

      var helper = new THREE.CameraHelper(this.camera);
      this.scene.add(helper);

      // x y z
      this.camera.position.set(3, 5, 10);
    },
    animate: function() {
      requestAnimationFrame(this.animate);
      //x,y,z
      this.shape[0].rotation.x += 0.005;
      this.shape[0].rotation.y -= 0.005;
      // this.shape[1].rotation.y += 0.015;
      // this.shape[1].rotation.z -= 0.005;
      // this.shape[2].rotation.z -= 0.025;
      // this.shape[2].rotation.x += 0.005;
      this.renderer.render(this.scene, this.camera);
    }
  }
};
</script>
<style scoped>
#d3d1 {
  height: 600px;
  width: 600px;
  margin: 0 auto;
}
</style>



